import React, { useState, useEffect } from 'react'
import { Card, CardBody, Heading, Text } from 'uikit'
import BigNumber from 'bignumber.js/bignumber'
import styled from 'styled-components'
import { getBalanceNumber } from 'utils/formatBalance'
import useMemberLength from 'hooks/useMemberLength'
import { useTotalSupply, useBurnedBalance } from 'hooks/useTokenBalance'
import { getTokenAddress } from 'utils/addressHelpers'
import CardValue from './CardValue'
import { useFarms, usePriceABnb, useTotalValue } from '../../../state/hooks'
import TextCardValue from './TextCardValue'

const StyledCakeStats = styled(Card)`
  margin-left: auto;
  margin-right: auto;
`

const Row = styled.div`
  align-items: center;
  display: flex;
  font-size: 14px;
  justify-content: space-between;
  margin-bottom: 8px;
`
const useFetch = (uri: string) => {
  const [data, setData] = useState(null)

  // empty array as second argument equivalent to componentDidMount
  useEffect(() => {
    async function fetchData() {
      const response = await fetch(uri)

      const json = await response.json()
      setData(json.result)
    }
    fetchData()
  }, [uri])

  return data
}

const deadBalance = (aAddress, address = '0x000000000000000000000000000000000000dead') =>
  `https://api.bscscan.com/api?module=account&action=tokenbalance&contractaddress=${aAddress}&address=${address}&tag=latest&apikey=VSHI3UDSF7XUCTQ5ZKY6SBQ985HACXNJUN`

const CakeStats = () => {
  const aAddress = getTokenAddress()
  const totalValue = useTotalValue()
  const totalAgents = useMemberLength()
  const totalSupply = useTotalSupply()
  const burnedBalance = useBurnedBalance(getTokenAddress())
  const farms = useFarms()
  const aPrice = usePriceABnb()
  const circSupply = totalSupply ? totalSupply.minus(burnedBalance) : new BigNumber(0)
  const cakeSupply = getBalanceNumber(circSupply)
  const marketCap = aPrice.times(circSupply)

  const balanceRaw = useFetch(deadBalance(aAddress))
  const balanceValue = getBalanceNumber(new BigNumber(balanceRaw)).toFixed(2)

  let tokenPerBLock = 0

  if (farms && farms[0] && farms[0].tokenPerBLock) {
    tokenPerBLock = new BigNumber(farms[0].tokenPerBLock).div(new BigNumber(10).pow(18)).toNumber()
  }

  return (
    <StyledCakeStats>
      <CardBody>
        <Heading size="lg" mb="24px">
          Top Secret Operation (TVL)
        </Heading>
        <CardValue value={totalValue.toNumber()} prefix="$" decimals={2} />
        <Row>
          <Text fontSize="14px">Total A009 Leaked</Text>
          {cakeSupply && <CardValue fontSize="14px" value={cakeSupply} decimals={2} />}
        </Row>
        <Row>
          <Text fontSize="14px">Total A009 Burned</Text>
          <TextCardValue fontSize="14px" value={balanceValue} />
        </Row>
        <Row>
          <Text fontSize="14px">A009 Price </Text>
          <CardValue fontSize="14px" value={aPrice.toNumber()} prefix="$" decimals={3}  />
        </Row>
        <Row>
          <Text fontSize="14px">Market Cap</Text>
          <CardValue fontSize="14px" value={getBalanceNumber(marketCap)} decimals={0} prefix="$" />
        </Row>
        <Row>
          <Text fontSize="14px">New A009/block</Text>
          <TextCardValue fontSize="14px" value={tokenPerBLock.toString()} />
        </Row>
        <Row>
          <Text fontSize="14px">Global Agents</Text>
          <TextCardValue fontSize="14px" value={totalAgents.toString()} />
        </Row>
      </CardBody>
    </StyledCakeStats>
  )
}

export default CakeStats
